///Section One

.section_container {
    margin-top: 10%;
    height: 560px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    .header_image {
        height: 100px;
        display: flex;
        flex-direction: column;

        img {
            width: 100%;
            padding-top: 20px;
        }
    }

    .section_card {
        align-self: center;
        justify-self: center;
        height: 80%;
        width: 50%;
    }
}


/* Features Page */

.features {
    .feature_cards {
        display: grid;
        grid-template-rows: 1fr;
        grid-template-columns: repeat(3,1fr);
        height: 450px;
        gap: 100px;
        justify-items: center;

        .card_container{
            width: 80%;
            height: 300px;
            .card_center {
                height: fit-content;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
            }
        }
    }
}



.features_2 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 0px 20px;

    .features_text {
        height: 100%;
        display: flex;
        flex-direction: column;

        .features_content {
            padding: 20px 0px;
            width: 60%;
        }
    }


    .features_image {
        display: flex;
        align-items: center;
        justify-items: center;
        width: 100%;
        height: 100%;

        img {
            width: 80%;
        }
    }
}


/* Support Page */


.contact_cards {
    display: flex;
    justify-content: space-evenly;
}

.contact_card {
    width: 28%;
    height: 8vh;
    background-image: linear-gradient(90deg, #5F6CAF 0%, #E0C3FC 100%);
    border-radius: 18px;
    
    display: flex;
    align-items: center;
    justify-content: center;

    li a{
        font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
        font-size: 16px;
        color: #F8F8F8;
        letter-spacing: 0;
        line-height: 37px;
        padding: 20px;
        text-decoration: none;
    }
}


//contact form 

.support_form {
    display: grid;
    grid-template-columns: 40% 60%;
    grid-template-rows: 100%;

    img  {
        width: 100%;
    }


    form {
        display: flex;
        padding-left: 100px;
        flex-direction: column;


        input {
            display: block;
            width: 80%;
            height: 52px;
            margin: 20px 0px 20px 0px;
            color: black;
            font-size: 18px;
            padding-left: 5px;
            background-color: transparent;
            border-image-source: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%);
            border-image-slice: 1;
        }

        textarea {
            width: 80%;
            background-color: transparent;
            border-image-source: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%);
            border-image-slice: 1;
            resize: none;
        }

        .support_buttons {
            display: flex;
            width: 30%;
             input {
                 margin: 20px;
                 border-radius: 16px;
             }

        }
    
        input::placeholder {
            color: lightgray;
            font-size: 16px;
            padding-left: 5px;
        }

        input[type=submit] {
            background-color: #8EC5FC;
            background-image: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%);
            color: $whiteText;
            font-size: 14px;
            border: none;
            cursor: pointer;
        }
    }
}