.menu {
  width: 120px;
  box-shadow: 0 4px 5px 3px rgba(0, 0, 0, 0.2);
  position: relative;
  display: none;

  .menu-options {
    list-style: none;
    padding: 10px 0;

    .menu-option {
      font-weight: 500;
      font-size: 14px;
      padding: 10px 40px 10px 20px;
      cursor: pointer;

      &:hover {
        background: rgba(0, 0, 0, 0.2);
      }
    }
  }
}





@keyframes move {
  0% {
    transform: scale(1)
  }

  38% {
    transform: scale(0.8, 1)
  }

  40% {
    transform: scale(0.8, 1)
  }

  78% {
    transform: scale(1.3)
  }

  80% {
    transform: scale(1.3)
  }

  100% {
    transform: scale(1)
  }
}